body{
  margin: 0;
  font-family: Roboto;
}
:root {
  --mdc-theme-primary: rgb(63, 81, 181);
  --mdc-theme-secondary: #e91e63;
  --mdc-typography-font-family: -apple-system, 'Roboto';
  
}

.mdc-floating-label--float-above, .mdc-text-feild--focused .mdc-floating-label{
  color: rgb(63, 81, 181)!important;
}

.mdc-text-field{
  border-radius: 4px!important;
  width: 100%;
}

.mdc-line-ripple::before{
  border: none!important
}


.mdc-text-field:last-child{
  margin-top: 8px
}

.rmwc-collapsible-list{
  padding-top: 4px
}
.rmwc-collapsible-list__children-inner li{
  padding-right:
}

.rmwc-collapsible-list__children-inner i{
  width: 24px
}

.navigate{
  padding-left: 6px!important;
  padding-right: 6px!important;
  --mdc-ripple-fg-scale: 1.5!important
}

.navigate i{
  margin-right: 0!important;
  margin-left: 0!important
}

.pagination span{
  align-self: center;
  line-height: 1.5;
  
}

button[disabled]::before{
  display: none;
}


.pagination{
  display: flex;
  margin-left: 16px;
  position: fixed;
  bottom: 0px

}

.mdc-drawer__content .mdc-ripple-upgraded:not(.mdc-ripple-upgraded--foreground-activation):not(.mdc-list-item--selected){
  background: transparent!important
}

.mdc-drawer__content .mdc-ripple-upgraded:not(.mdc-ripple-upgraded--foreground-activation):not(.mdc-list-item--selected)::before{
  display: none;
}

.mdc-drawer__content .mdc-list-item{
  border-radius: 4px!important;
  overflow: hidden!important;
  -webkit-mask-image: -webkit-radial-gradient(white, black)
}


.mdc-fab{
  bottom: 16px;
  right: 16px;
  position: fixed!important;
  overflow: hidden!important;
  margin-bottom: constant(safe-area-inset-top)
}